.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}


.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}


.App-link {
  color: #61dafb;
}


.checkbox-orange {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  width: 20px;
  height: 20px;
  border: 2px solid #d1d5db; /* gray border */
  border-radius: 4px;
  outline: none;
  cursor: pointer;
  position: relative;
}

.checkbox-orange:checked {
  background-color: orange; /* orange background when checked */
  border: 2px solid orange; /* orange border */
}

.checkbox-orange:checked::after {
  content: '';
  position: absolute;
  left: 4px;
  top: 4px;
  width: 6px;
  height: 15px;
  border: solid black; /* white check */
  border-width: 0 2px 2px 0; /* create the check shape */
  transform: rotate(45deg); /* rotate to create a checkmark */
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@media (max-width: 768px) {
  nav {
    background-color: white; /* Adjust background for mobile */
  }
}